.App {
  text-align: center;
  background: rgb(4,0,80);
  background: linear-gradient(180deg, rgba(4,0,80,1) 0%, rgb(68, 67, 0) 100%);
  background-repeat: no-repeat;
  background-attachment: scroll;
  color: white;
  height: 100vh;
  width: 100vw;
  float: top;
  overflow-x: hidden;
  
  
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 01s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
